import { Form, Check } from "react-bootstrap";
import React, { Component } from "react";
import { useState } from "react";

export default function BatchInput({ childToParent }) {
  const [batch, setBatch] = useState("");
  const [showbatch, showBatch] = useState("");

  function handleChange(event) {
    setBatch(event.target.value);
  }

  return (
    <>
      <p>Input batch: </p>
      <input type="text" name="batch" onChange={handleChange} value={batch} />
      <button onClick={() => childToParent(batch)}>Submit</button>
    </>
  );
}
