import "./styles.css";
import PageHeading from "./compenents/pageHeading";
import BatchInput from "./compenents/batchInput";
import { useState } from "react";
import { batchNumbers } from "./data/batchNumbers";
import BatchDetailPage from "./compenents/batchDetailPage";
import PageDescription from "./compenents/pageDescription";

export default function App() {
  const [batch, setBatch] = useState("");

  const childToParent = (batch) => {
    setBatch(batch);
  };
  return (
    <div className="App">
      <div
        style={{
          "max-width": "1000px",
          "margin-left": "auto",
          "margin-right": "auto"
        }}
      >
        <PageHeading />

        <PageDescription />

        <BatchInput childToParent={childToParent} />
        <p>
          {batchNumbers.includes(batch) ? (
            <BatchDetailPage batch={batch} />
          ) : batch === "" ? (
            "Select Batch Number"
          ) : (
            "Batch Number Not Found"
          )}
        </p>
      </div>
    </div>
  );
}
