export default function GrowingDetails(props) {
  var growingDetailsData = props.batchdetails[props.batch];
  return (
    <>
      <p>-----</p>
      <p>growing details for batch {props.batch}</p>
      <p>Key crop: {growingDetailsData.keyCrop}</p>
      <p>Date of harvesting: {growingDetailsData.dateOfHarvesting}</p>
      <p>Growing Location: {growingDetailsData.placeOfHarvesting}</p>
      <p>Harvester: {growingDetailsData.harvester}</p>
      <img width="400px" src={growingDetailsData.imageGrowing} alt="" />
      <video width="100%" src={growingDetailsData.videoGrowing} controls />
      <p>-----</p>
    </>
  );
}
