export default function OilDetails(props) {
  var oilsDetailsData = props.batchdetails[props.batch];

  return (
    <>
      <p>----</p>
      <p>Oil details for batch {props.batch}</p>
      <p>date of QC: {oilsDetailsData.dateOfQC}</p>
      <p>type of test: {oilsDetailsData.typeOfTest}</p>
      <p>Location of test: {oilsDetailsData.locationOfTest}</p>
      <p>person who tested: {oilsDetailsData.tester}</p>
      <img width="400px" src={oilsDetailsData.imageOil} alt="" />
      <video width="100%" src={oilsDetailsData.videoTesting} controls />

      <p>----</p>
    </>
  );
}
