import BottleNumbers from "./batchDetailPageComponenets/bottleNumberDetails";
import BottlingDetails from "./batchDetailPageComponenets/bottlingDetails";
import CompoundDetails from "./batchDetailPageComponenets/compoundDetails";
import GrowingDetails from "./batchDetailPageComponenets/growingDetails";
import OilDetails from "./batchDetailPageComponenets/oilDetails";
import PerfumeDetails from "./batchDetailPageComponenets/perfumeDetails";
import { BatchDetails } from "../data/batchDetails";
import { Card } from "react-bootstrap";

export default function BatchDetailPage(props) {
  var batchDetails = BatchDetails;

  return (
    <>
      <h2>Batch Number: {props.batch}</h2>
      <Card style={{ margin: "5%" }}>
        <Card.Title>Bottle Numbers</Card.Title>
        <Card.Body>
          <BottleNumbers batch={props.batch} batchdetails={batchDetails} />
        </Card.Body>
      </Card>{" "}
      <PerfumeDetails batch={props.batch} batchdetails={batchDetails} />
      <Card.Title>Perfume Details</Card.Title>
      <Card style={{ margin: "5%" }}>
        <Card.Title>Growing Crop Details</Card.Title>
        <Card.Body>
          <GrowingDetails batch={props.batch} batchdetails={batchDetails} />
        </Card.Body>{" "}
      </Card>
      <Card style={{ margin: "5%" }}>
        <Card.Title>Oil Details</Card.Title>
        <Card.Body>
          <OilDetails batch={props.batch} batchdetails={batchDetails} />
        </Card.Body>{" "}
      </Card>
      <Card style={{ margin: "5%" }}>
        <Card.Title>Compound Details</Card.Title>
        <Card.Body>
          <CompoundDetails batch={props.batch} batchdetails={batchDetails} />
        </Card.Body>{" "}
      </Card>
      <Card style={{ margin: "5%" }}>
        <Card.Title>Bottling Details</Card.Title>
        <Card.Body>
          <BottlingDetails batch={props.batch} batchdetails={batchDetails} />
        </Card.Body>{" "}
      </Card>
    </>
  );
}
