import { bottleNumbers } from "../../data/bottleNumbers";

export default function BottleNumbers(props) {
  var bottleNumbersPage = bottleNumbers[props.batch];
  return (
    <>
      <p>-----</p>
      <p>These are the bottles from batch number {props.batch}: </p>
      <ul>
        {bottleNumbersPage.map((number) => (
          <li style={{ display: "inline" }}>
            <a>{number} </a>
          </li>
        ))}
      </ul>

      <p>-----</p>
    </>
  );
}
