import { Card, Col, Row } from "react-bootstrap";

export default function PerfumeDetails(props) {
  return (
    <Row xs={1} md={2}>
      <Col>
        <Card style={{ margin: "10%" }}>
          <Card.Body>
            <p>----</p>
            <p>perfume details for batch {props.batch}</p>
            <p>show image of perfume bottle from website - example image</p>
            <p>key fragrance notes</p>
            <p>----</p>
          </Card.Body>
        </Card>
      </Col>
      <Col>
        <img width="100%" alt="" src={props.batchdetails[props.batch].image} />
        <p>key fragrance notes</p>
        <p>----</p>
      </Col>
    </Row>
  );
}
