export default function CompoundDetails(props) {
  var compoundDetailsData = props.batchdetails[props.batch];
  return (
    <>
      <p>-----</p>
      <p>compound details for batch {props.batch}</p>
      <p>compounding date: {compoundDetailsData.dateOfCompounding}</p>
      <img width="400px" src={compoundDetailsData.imageCompounding} alt="" />
      <video width="100%" src={compoundDetailsData.videoCompounding} controls />
      <p>-----</p>
    </>
  );
}
