export default function BottlingDetails(props) {
  return (
    <>
      <p>----</p>
      <p>bottling details for batch {props.batch}</p>
      <p>Bottled on: {props.batchdetails[props.batch].dateOfBottling}</p>
      <img
        width="400px"
        src={props.batchdetails[props.batch].imageBottling}
        alt=""
      />
      <div>
        <video
          width="400px"
          src={props.batchdetails[props.batch].videoBottling}
          controls
        />
      </div>
      <p>----</p>
    </>
  );
}
